body, p, h1 {
  margin: 0;
}

ul {
  margin: 0;
  padding-inline-start: 0;
}

* {
  box-sizing: border-box;
}

html {
  --main-lc: #00acdc;
  width: 100vw;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 15px;
}

.header {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.logo img {
  display: block;
}

.nav {
  margin-right: 50px;
  display: flex;
}

.nav_list {
  list-style: none;
  display: inline-block;
}

.nav_item {
  text-transform: uppercase;
  align-self: center;
  padding-left: 17px;
  display: inline-block;
}

.nav_item:first-child {
  padding-left: 0;
}

.nav_link {
  color: #000;
  text-align: center;
  height: 60px;
  padding: 22.5px 0;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav_link[data-qa="hover"]:hover, .nav_link.is-active {
  color: var(--main-lc);
}

.nav_link.is-active:after {
  content: "";
  background-color: var(--main-lc);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}
